.popup {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.5);
    flex-direction: row;
    justify-content: center;
    align-items: center;
    visibility: hidden;
    display: flex;
    opacity: 0;
    transition: visibility 1s, opacity 1s;
}